@mixin for-m-desktop-down {
  @media (max-width: 1380px) {
    @content;
  }
}

@mixin for-m-tablet-portrait-down {
  @media (max-width: 720px) {
    @content;
  }
}

@mixin for-l-phone-portrait {
  @media (max-width: 660px) {
    @content;
  }
}

@mixin for-m-phone-portrait {
  @media (max-width: 400px) {
    @content;
  }
}

.directory-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px -10px black;
  color: black;
  cursor: pointer;

  .directory-card__img {
    width: 225px;
    height: 225px;
    border-radius: 16px;
    @include for-m-desktop-down {
      width: 175px;
      height: 175px;
    }
    @include for-m-tablet-portrait-down {
      width: 225px;
      height: 225px;
    }
  }
  .directory-card__img-albumspage {
    width: 400px;
    height: 400px;
    border-radius: 16px;
    @include for-m-desktop-down {
      width: 325px;
      height: 325px;
    }

    @include for-m-tablet-portrait-down {
      width: 275px;
      height: 275px;
    }
    @include for-l-phone-portrait {
      width: 225px;
      height: 225px;
      margin: 1rem 0;
    }

    @include for-m-phone-portrait {
      width: 200px;
      height: 200px;
      margin: 1rem 0;
    }
  }

  .directory-card-info {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    cursor: help;
    padding: 2rem 2rem;
    text-align: justify;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    clip-path: circle(5% at 356px 48px);
    transition: clip-path 500ms ease-in-out, background-color 500ms ease-in-out;
    @include for-m-desktop-down {
      clip-path: circle(5% at 305px 42px);
    }
    @include for-m-tablet-portrait-down {
      display: none;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.8);
      clip-path: circle(90%);

      .directory-card-icon {
        opacity: 0;
      }
    }
    @include for-m-desktop-down {
      &:focus {
        background-color: rgba(255, 255, 255, 0.8);
        clip-path: circle(90%);

        .directory-card-icon {
          opacity: 0;
        }
      }
    }

    h2 {
      font-size: 2rem;
      margin: 0.2rem;
      text-align: center;
    }

    .directory-card-icon {
      position: absolute;
      top: 25px;
      right: 40px;
      font-size: 2rem;
      font-weight: 700;
      opacity: 1;
      transition: opacity 500ms ease-in-out;
      @include for-m-desktop-down {
        right: 15px;
        top: 20px;
      }
    }
  }
}
